import { refs } from './refs';

window.addEventListener('load', () => {
  setTimeout(() => {
    refs.loader.classList.add('done');
  }, 300);
});

const showSpiner = () => {
  refs.loader.classList.remove('done');
};

const hideSpiner = () => {
  refs.loader.classList.add('done');
};

export { showSpiner, hideSpiner };
